import React from "react";
import "./card.css";

function card() {
  const toggleDetails = () => {
    const para = document.querySelector(".para");
    const details = document.querySelector(".toggle-content");
    const angle = document.querySelector("#toggle-icon");

    if (para.style.display === "block") {
      para.style.display = "none";
      details.innerHTML = "More Details ";
      angle.className = "fas fa-angle-down";
    } else {
      para.style.display = "block";
      details.innerHTML = "Hide Details ";
      angle.className = "fas fa-angle-up";
    }
  };
  return (
    <div className="card">
      <div className="details">
        <h4> Honda city</h4>
        <p>Posted at 1 Nov, 9.30AM</p>
      </div>
      <div className="list">
        <div className="listElement">
          <div className="listIcon">
            <i class="bi bi-box"></i>
          </div>
          <div className="listSpecification">Specification 01</div>
        </div>
        <div className="listElement">
          <div className="listIcon">
            <i class="fas fa-truck-moving"></i>
          </div>
          <div className="listSpecification">Specification 02</div>
        </div>
        <div className="listElement">
          <div className="listIcon">
            <i class="fas fa-credit-card"></i>
          </div>
          <div className="listSpecification">Specification 03</div>
        </div>
      </div>
      <div className="briefing">
        <div className="para">
          Lorem ipsum dolor sit, amet consectetur adipisicing elit. Provident.
          Lorem ipsum dolor sit amet consectetur adipisicing elit. Quidem,
          tenetur.
        </div>
        <div className="toggle" onClick={toggleDetails}>
          <span className="toggle-content">More details </span>
          <span className="toggle-icon">
            <i id="toggle-icon" className="fas fa-angle-down"></i>
          </span>
        </div>
      </div>
      <div className="personals">
        <div className="picture">
          <div></div>
        </div>
        <div className="name">Rohan Sharma</div>
        <div className="location">Bengaluru, Karnataka</div>
      </div>
    </div>
  );
}

export default card;
