import React from "react";
import "./bid.css";

function bid() {
  const formHandler = () => {
    const bidDetails = document.querySelector(".bid-details");
    bidDetails.style.display = "block";
  };

  const buttonHandler = () => {
    const EnteredName = document.querySelector("#enteredName");
    const EnteredNumber = document.querySelector("#enteredNumber");
    const Button = document.querySelector("#bidNowButton");
    if (EnteredName.checkValidity() && EnteredNumber.checkValidity()) {
      Button.style.backgroundColor = "#3054D6";
      Button.style.color = "#E0E0E0";
    } else {
      Button.style.backgroundColor = "#EDF0F7";
      Button.style.color = "#B0C2F8";
    }
  };

  return (
    <>
      <div className="bid">
        <div className="bid-price">
          <div className="bid-heading">
            <h2>Place your Load Bid</h2>
            <div className="horizontal-line"></div>
          </div>
          <div className="bid-price-enter">
            <div className="rupeeSymbol">&#x20B9;</div>
            <div className="bidInput">
              <input type="number" placeholder="0" maxlength="10" />
            </div>
          </div>
          <div className="bid-radio-select">
            <input type="radio" name="radio" id="opt1" onChange={formHandler} />
            <label for="opt1" className="label1">
              <span>Fixed Price</span>
            </label>
            <input type="radio" name="radio" id="opt2" onChange={formHandler} />
            <label for="opt2" className="label2">
              <span>Rate Negotiable</span>
            </label>
          </div>
        </div>
        <div className="bid-details">
          <div className="inputfield">
            <label className="custom-field one">
              <input
                type="number"
                required
                min="5999999999"
                max="9999999999"
                id="enteredNumber"
                onChange={buttonHandler}
              />
              <span className="placeholder">Enter your mobile number *</span>
            </label>
          </div>
          <div className="inputfield">
            <label className="custom-field one">
              <input
                type="text"
                required
                id="enteredName"
                onChange={buttonHandler}
              />
              <span className="placeholder">Enter your name *</span>
            </label>
          </div>
          <div className="inputfield">
            <label className="custom-field one">
              <input type="text" required />
              <span className="placeholder">Enter Remarks (optional)</span>
            </label>
          </div>
        </div>
      </div>
      <div className="button" id="bidNowButton">
        bid now
      </div>
    </>
  );
}

export default bid;
